function Box(props) {
  const mesh = useRef();
  //useFrame(() => (mesh.current.rotation.x = mesh.current.rotation.y += 0.01));
  return (
     <mesh {...props} ref={mesh}>
        <boxGeometry args={[0.5, 0.5, 0.5]} />
        <meshStandardMaterial color={"orange"} />
     </mesh>
  );
}

function Ground(props) {
  const [floor, normal] = useTexture(['/concrete-polished.jpg', '/Glass_Frosted_001_normal.jpg'])
  return (
    <Reflector resolution={1024} args={[2, 2]} {...props}>
      {(Material, props) => <Material color="#f0f0f0" metalness={.5} roughness={.2} roughnessMap={floor} roughnessScale={[0.1, 0.1]} normalMap={normal} normalScale={[0.1, 0.1]} {...props} opacity={0.8} envMapIntensity={0} renderOrder={-1}/>}
    </Reflector>
  )
}

export default function App({ }) {

  const lightRef = useRef()
  const spotLight = useRef()
  
  return (

    
    <Canvas gl={{ alpha: false }} dpr={[1, 1.5]} >
      <color attach="background" args={['#000000']} />
      {/* <fog attach="fog" args={['#191920', 0, 15]} /> */}
      <Suspense fallback={null}>
        <Environment // Whether to affect scene.background
                files={'reflection-map_4K_update.hdr'}
                path={'/'}
        />
        <ambientLight intensity={.15} ref={lightRef}/>
        <directionalLight ref={spotLight} intensity={1} position={[1, -.02, -0.410]} color={'#ff0000'}/>
        <Background/>
        <group position={[0, 0, 0]} >
          <Model scale={[.04,.04,.04]} position={[0, -0.6, 0]}/>
          <Ground mirror={1} blur={[300, 50]} mixBlur={12} mixStrength={1.5} rotation={[-Math.PI / 2, 0, Math.PI / 2]} position-y={-0.6} />
        </group>
      </Suspense>
      <PerspectiveCamera makeDefault fov={45} position={[0, 0, 4]}></PerspectiveCamera>
      {/* <Controls autoRotate={rotateCube}/> */}
      <Controls />

      <EffectComposer multisampling={8}>
          <Bloom kernelSize={3} luminanceThreshold={0} luminanceSmoothing={0.4} intensity={0.2} />
          <Bloom kernelSize={KernelSize.HUGE} luminanceThreshold={0} luminanceSmoothing={0} intensity={0.6} />
      </EffectComposer>
      
      <directionalLight 
        position={[4, -4, 3]}
        intensity={.25}
      />
      
      <directionalLight 
        position={[-3, -4, 3]}
        intensity={.25}
      />
      
      <directionalLight 
        position={[0, .5, -3]}
        intensity={.25}
      />
    </Canvas>
 
  )
}

function Frames({ images, q = new THREE.Quaternion(), p = new THREE.Vector3() }) {
  const ref = useRef()
  const clicked = useRef()
  const [, params] = useRoute('/item/:id')
  const [, setLocation] = useLocation()
  useEffect(() => {
    clicked.current = ref.current.getObjectByName(params?.id)
    if (clicked.current) {
      clicked.current.parent.updateWorldMatrix(true, true)
      clicked.current.parent.localToWorld(p.set(0, GOLDENRATIO / 2, 1.25))
      clicked.current.parent.getWorldQuaternion(q)
    } else {
      p.set(0, 0, 5.5)
      q.identity()
    }
  })
  useFrame((state, dt) => {
    state.camera.position.lerp(p, 0.025)
    state.camera.quaternion.slerp(q, 0.025)
  })
  return (
    <group
      ref={ref}
      onClick={(e) => (e.stopPropagation(), setLocation(clicked.current === e.object ? '/' : '/item/' + e.object.name))}
      onPointerMissed={() => setLocation('/')}>
      {images.map((props) => <Frame key={props.url} {...props} /> /* prettier-ignore */)}
    </group>
  )
}

function Frame({ url, c = new THREE.Color(), ...props }) {
  const [hovered, hover] = useState(false)
  const [rnd] = useState(() => Math.random())
  const image = useRef()
  const frame = useRef()
  const name = getUuid(url)
  useCursor(hovered)
  useFrame((state) => {
    image.current.material.zoom = 2 + Math.sin(rnd * 10000 + state.clock.elapsedTime / 3) / 2
    image.current.scale.x = THREE.MathUtils.lerp(image.current.scale.x, 0.85 * (hovered ? 0.85 : 1), 0.1)
    image.current.scale.y = THREE.MathUtils.lerp(image.current.scale.y, 0.9 * (hovered ? 0.905 : 1), 0.1)
    frame.current.material.color.lerp(c.set(hovered ? 'orange' : 'white'), 0.1)
  })
  return (
    <group {...props}>
      <mesh
        name={name}
        onPointerOver={(e) => (e.stopPropagation(), hover(true))}
        onPointerOut={() => hover(false)}
        scale={[1, GOLDENRATIO, 0.05]}
        position={[0, GOLDENRATIO / 2, 0]}>
        <boxGeometry />
        <meshStandardMaterial color="#151515" metalness={0.5} roughness={0.5} envMapIntensity={.2} />
        <mesh ref={frame} raycast={() => null} scale={[0.9, 0.93, 0.9]} position={[0, 0, 0.2]}>
          <boxGeometry />
          <meshBasicMaterial toneMapped={false} fog={false} />
        </mesh>
        <Image raycast={() => null} ref={image} position={[0, 0, 0.7]} url={url} />
      </mesh>
      <Text maxWidth={0.1} anchorX="left" anchorY="top" position={[0.55, GOLDENRATIO, 0]} fontSize={0.025}>
        {name.split('-').join(' ')}
      </Text>
    </group>
  )
}
